.backButton {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    background-color: rgb(255, 255, 255);
    box-shadow: var(--boxShadow);
    position: absolute;
    top: 20px;
    left: 20px;

    width: 45px;
    height: 45px;

    &:hover {
        opacity: .7;
        transition: all .2s ease-in-out;
        cursor: pointer;
    }
}